export default {
	data() {
		return {
			scale: 2,
			old: {
				x: 0,
				y: 0,
				scale: 2
			},
			height:600,
			pathArr:[],
			page:0
		}
	},
	components: {
		
	},
	methods: {
		onScale: function(e) {
			this.old.scale = e.detail.scale
		},
		jian(){
			console.log('jian');
			if(this.page!=0){
				this.page--;
			}
		},
		add(){
			console.log('add');
			if(this.page!=this.pathArr.length-1){
				this.page++;
			}
		}
	},
	onLoad: function(option) {
		console.log(option.id);
		const res = uni.getSystemInfoSync();
		console.log(res.windowHeight);
		this.height = res.windowHeight;
		uni.request({
			url: this.apiServer + '/basicdata/repPic.php',
			data: {
				id: option.id
			},
			method: 'POST',
			dataType: 'json',
			header: {
				'content-type': 'application/x-www-form-urlencoded', //POST头信息
			},
			success: (res) => {
				console.log(res.data);
				res=res.data;
				var arr=(res.jcbg||'').split("||");
				arr.splice(0,1);
				for(var i=0;i<arr.length;i++){
					var path=this.apiServer+'/uploads/'+arr[i];
					console.log(path);
					this.pathArr.push(path);
				}
			},
			fail: (error) => {
				console.log(error);
			}
		});
	}
}
